















import { Component, Vue, Prop } from 'vue-property-decorator';
import Modal from '@/components/common/Modal.vue';
import ExerciseApi from '@/services/api/ExerciseApi';
import Exercise from '@/models/Exercise';
import ExerciseConstraint from '@/models/ExerciseConstraint';
import { ExerciseConstraintType } from '@/enums/ExerciseConstraintType';

const exerciseApi = new ExerciseApi();
@Component({
    components: {
        Modal,
    },
})
export default class DeleteExercise extends Vue {
    private id: string;

    public constructor() {
        super();
        this.id = '';
    }

    public close(): void {
        // @ts-ignore
        this.$refs.modal.showModal = false;
    }

    public submit(): void {
        // @ts-ignore
        this.$refs.modal.showModal = false;
        exerciseApi.delete(this.id);

        this.$emit('submit');
    }

    public open(id: string): void {
        this.id = id;
        // @ts-ignore
        this.$refs.modal.showModal = true;
    }
}
