










































import { Component, Vue, Prop } from 'vue-property-decorator';
import Modal from '@/components/common/Modal.vue';
import ExerciseApi from '@/services/api/ExerciseApi';
import Exercise from '@/models/Exercise';
import ExerciseConstraint from '@/models/ExerciseConstraint';
import { ExerciseConstraintType } from '@/enums/ExerciseConstraintType';

const exerciseApi = new ExerciseApi();
@Component({
    components: {
        Modal,
    },
})
export default class CreateExercise extends Vue {
    private name: string;
    private constraints: ExerciseConstraint[];

    public constructor() {
        super();

        this.name = '';
        this.constraints = [
            new ExerciseConstraint(ExerciseConstraintType.Weight, ''),
        ];
    }

    public get constraintTypes(): any[] {
        const items: any[] = [];
        const values = Object.keys(ExerciseConstraintType).filter(
            (type) => isNaN(type as any) && type !== 'values',
        );

        if (values.length < 0) {
            return items;
        }

        for (const item of values) {
            // @ts-ignore
            items.push(({ name: item, value: ExerciseConstraintType[item]}));
        }

        return items;
    }

    public addConstraint(): void {
        this.constraints.push(new ExerciseConstraint(ExerciseConstraintType.Weight, ''));
    }

    public close(): void {
        // @ts-ignore
        this.$refs.modal.showModal = false;
    }

    public submit(): void {
        // @ts-ignore
        this.$refs.modal.showModal = false;

        exerciseApi.post(new Exercise(this.name, this.constraints));

        this.$emit('submit');
    }

    public open(): void {
        this.name = '';
        this.constraints = [
            new ExerciseConstraint(ExerciseConstraintType.Weight, ''),
        ];

        // @ts-ignore
        this.$refs.modal.showModal = true;
    }
}
