import StorageApi from './StorageApi';
import IStorageItem from '@/models/IStorageItem';
import Utils from '../Utils';

export default class Api<T extends IStorageItem> {
    private storageApi: StorageApi;
    private namespace: string;

    public constructor(namespace: string) {
        this.namespace = namespace;
        this.storageApi = new StorageApi();
    }

    public get(): T[] {
        const items = this.storageApi.getItem<T[]>(this.namespace);

        if (items == null) {
            return [] as T[];
        }

        return items;
    }

    public getSingle(id: string): T | null {
        const currentItems = this.get();
        const index = currentItems.findIndex((i: T) => i.id === id);

        if (index < 0) {
            return null;
        }

        return currentItems[index];
    }

    public put(id: string, item: T): void {
        const currentItems = this.get();
        const index = currentItems.findIndex((i: T) => i.id === id);

        if (index < 0) {
            return;
        }

        currentItems[index] = item;

        this.storageApi.setItem(this.namespace, currentItems);
    }

    public post(item: T): void {
        const currentItems = this.get();
        item.id = Utils.Guid();
        currentItems.push(item);

        this.storageApi.setItem(this.namespace, currentItems);
    }

    public delete(id: string) {
        const currentItems = this.get();
        const index = currentItems.findIndex((i: T) => i.id === id);

        if (index < 0) {
            return;
        }

        currentItems.splice(index, 1);

        this.storageApi.setItem(this.namespace, currentItems);
    }
}
