










































import { Component, Vue, Watch } from 'vue-property-decorator';
import Authenticated from '@/layouts/Authenticated.vue';
import CreateExercise from '@/components/exercise/modals/create-exercise-modal.vue';
import EditExercise from '@/components/exercise/modals/edit-exercise-modal.vue';
import DeleteExercise from '@/components/exercise/modals/delete-exercise-modal.vue';
import Exercise from '@/models/Exercise';
import ExerciseApi from '@/services/api/ExerciseApi';

const exerciseApi = new ExerciseApi();

@Component({
    components: {
        Authenticated,
        CreateExercise,
        EditExercise,
        DeleteExercise,
    },
})
export default class Exercises extends Vue {
    private exercises: Exercise[] = [];
    public openCreateModal(): void {
        // @ts-ignore
        this.$refs.createModal.open();
    }

    public openEditModal(exercise: Exercise): void {
        // @ts-ignore
        this.$refs.editModal.open(exercise.id);
    }

    public openDeleteModal(exercise: Exercise): void {
        // @ts-ignore
        this.$refs.deleteModal.open(exercise.id);
    }

    public refreshExercises(): void {
        this.exercises = exerciseApi.get();
    }

    public submitCreateModal(): void {
        this.refreshExercises();
    }

    public submitEditModal(): void {
        this.refreshExercises();
    }

    public submitDeleteModal(): void {
        this.refreshExercises();
    }

    public mounted(): void {
        this.refreshExercises();
    }
}
