import { DateTime } from 'luxon';
import { ExerciseConstraintType } from '@/enums/ExerciseConstraintType';

export function DateFilter(isoString: string): string | null {
    if (isoString == null) {
        return null;
    }

    return DateTime.fromISO(isoString).toFormat('dd/MM/yyyy');
}

export function DateTimeFilter(isoString: string): string | null {
    if (isoString == null) {
        return null;
    }

    return DateTime.fromISO(isoString).toFormat('dd/MM/yyyy HH:mm');
}

export function TimeFilter(isoString: string): string | null {
    if (isoString == null) {
        return null;
    }

    return DateTime.fromISO(isoString).toFormat('HH:mm');
}

export function ExerciseConstraintTypeNameFilter(type: ExerciseConstraintType): string {
    return ExerciseConstraintType[type];
}
